<template>
  <div
    class="side-catalog"
    :style="{
      'right': right,
      'top': top,
      'left': left,
      'bottom': bottom
    }"
  >
    <div
      v-for="(item) in sideCatalogList"
      :key="item.ref"
      class="side-catalog__item"
      @click="anchorActive(item.ref)"
    >
      <i
        v-if="finished[item.ref]"
        class="el-icon-success side-catalog__item-finished"
      />
      <i
        class="side-catalog__item-icon"
        :class="{
          'side-catalog__item-icon--active': active===item.ref,
          'side-catalog__item-icon--child': item.child
        }"
      />
      <span
        :style="{'margin-left': getTitleMargin(item.level)}"
        class="side-catalog__item-label"
        :class="{'side-catalog__item-label--active': active===item.ref}"
      >{{ item.label }}</span>
    </div>
  </div>
</template>
<script>
import debounce from 'lodash.debounce';
import throttle from 'lodash.throttle';
export default {
  name: 'SideCatalog',
  props: {
    right: {
      type: String,
      default: '100px',
    },
    top: {
      type: String,
      default: '100px',
    },
    left: {
      type: String,
      default: 'initial',
    },
    bottom: {
      type: String,
      default: 'initial',
    },
    sideCatalogList: {
      type: Array,
      default() {
        return [
          // {
          //   label: 'name',
          //   ref: 'refname', //f
          //   level: 1,  //默认为1
          // },
        ];
      },
    },
    // 是否开启dom监听,dom有变化主动更新各个ref的offsetTop值
    openDomWatch: {
      type: Boolean,
      default: false,
    },
    // 绑定scroll事件的dom的class
    // 该元素必须为定位元素或者最近的 table,td,th,body
    scrollElementClass: {
      type: String,
      default: '',
    },
    /*
    特殊配置
    */
    finished: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      active: '',
      refTopMap:{},
      refTopList: [],
      refList: [],
      isBeforeDestroy: false,
      observer: null,
    };
  },
  computed: {
    scrollElement() {
      return this.scrollElementClass
        ? document.querySelector(this.scrollElementClass)
        : window;
    },
    scrollToEle(){
      return this.scrollElementClass
        ? this.scrollElement
        : document.documentElement;
    }

  },
  mounted() {
    this.initActive();
    this.setOffsetParent();
    this.setRefList();
    this.scrollElement.addEventListener('scroll', throttle(this.scrollHandle, 200));
    this.$nextTick(() => {
      setTimeout(() => {
        if(this.openDomWatch) {
        // 设置dom监听
          // const parentDom = this.$parent.$el
          this.observer = new MutationObserver(debounce(this.setRefList, 700));
          this.observer.observe(this.scrollElement, {
            childList: true,
            subtree: true,
          });
        }
        this.setRefList();
        // this.scrollElement.addEventListener('scroll', throttle(this.scrollHandle, 250));
      }, 500);
    });
  },
  beforeDestroy() {
    if(this.openDomWatch) {
      // beforeDestroy时,解绑dom监听之前,偶尔会触发observer监听的setRefList函数
      // 导致报错,需要用变量控制
      this.isBeforeDestroy = true;
      // 解绑dom监听
      this.observer.disconnect();
    }
    this.scrollElement.removeEventListener('scroll', this.scrollHandle);
  },
  methods: {
    // 点击锚点
    anchorActive(ref) {
      if(this.active === ref) return;
      this.scrollElement.removeEventListener('scroll', this.scrollHandle);
      
      // this.getRefDom(ref).scrollIntoView();
      this.scrollToEle.scrollTop = this.refTopMap[ref];
      
      this.active = ref;
    
      // setTimeout(() => {
        this.scrollElement.addEventListener('scroll', throttle(this.scrollHandle, 200));
      // }, 300);
    },
    // 获取ref的dom
    getRefDom(_ref) {
      /**
       * 获取ref的dom元素有以下四种情况
       * 1. ref在循环中, ref是dom元素 => ref[0]
       * 2. ref在循环中, ref是vue实例 => ref[0].$el
       * 3. ref不在循环中, ref是dom元素 => ref
       * 4. ref不在循环中, ref不是vue实例 => ref.$el
       */
      const ref = this.$parent.$refs[_ref];
      if(Array.isArray(ref)) {
        return this.vueOrDom(ref[0]);
      }
      return this.vueOrDom(ref);
    },
    // ref 是vue还是dom
    vueOrDom(ref) {
      if(ref instanceof HTMLElement) return ref;
      if(ref._isVue) return ref.$el;
    },
    // 获取ref offsetTop数组
    setRefList() {
      if(this.isBeforeDestroy) return;
      this.refList = [];
      this.refTopList = [];
      this.sideCatalogList.forEach((item) => {
        const offsetTop = this.getRefDom(item.ref).offsetTop;
        console.log(offsetTop);
        
        this.refList.unshift(item.ref);      
        this.refTopList.unshift(offsetTop);
        this.refTopMap[item.ref] = offsetTop;
      });
    },
    // 滚动回调
    scrollHandle(e) {
      const scrollTop = this.scrollElementClass ? e.target.scrollTop : document.documentElement.scrollTop;
      this.refTopList.some((item, index) => {
        if(scrollTop >= item) {
          this.active = this.refList[index];
          return true;
        }
        return false;
      });
    },
    initActive() {
      this.active = this.sideCatalogList[0].ref;
    },
    getTitleMargin(level) {
      return level ? `${parseInt(level, 10) * 10}px` : '10px';
    },
    // 需要为scrollElement设置相对定位(offsetParent)
    // offsetParent(定位元素或者最近的 table,td,th,body)
    setOffsetParent(){
      if(!this.scrollElementClass) return;
      const ele = document.querySelector(this.scrollElementClass);
      if(ele.style.position) return;
      ele.style.position = 'relative';      
    }
  },
};
</script>
<style scoped lang="scss" src="./main.scss"></style>